import { Component, ViewChild, OnInit } from '@angular/core';
import {SearchComponent} from './search/search.component';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [SearchComponent]
})
export class AppComponent implements OnInit {

  private accessPointUrl = 'https://localhost:5001/api/users';
  constructor(private SearchComp: SearchComponent, private http: HttpClient) {}
  Collections = [];
  SearchResults;
  DisplayLogin = false;
  LoggedIn = false;
  error;
  async ngOnInit() {
    try {
      this.http.get(
        this.accessPointUrl,
        {headers: {'Content-Type': 'application/json'}}).subscribe(
        result => {
          console.log(result);
          if (result === true) {
            this.LoggedIn = true;
          } else {
            this.error = "Incorrect Email or Password."
          }
        }
      );
    } catch (err) {
      console.log(err);
    }
  }
  ReceiveMessage() {
    {this.DisplayLogin ? this.DisplayLogin = false : this.DisplayLogin = true; }
  }
  Clicked() {
    this.SearchComp.ClearSearch();
  }
  DismissLogin() {
    this.DisplayLogin = false;
  }
  Logout() {
    try {
      this.http.delete(
        this.accessPointUrl,
        {headers: {'Content-Type': 'application/json'}}).subscribe(
        result => {
          this.LoggedIn = false;
        }
      );
    } catch (err) {
      console.log(err);
    }
  }
}
